import React from 'react'
import { LifeIcon } from './icons'
import { useStickyState } from './use-sticky-state'
import { Link, useHistory } from 'react-router-dom'
/** @jsx jsx */
import { css, jsx } from '@emotion/core'
import { theme } from './theme'

export function Layout({
  children,
  title,
  gameType,
}: {
  children: React.ReactNode
  title?: string
  gameType?: 'HUNTER' | '301'
}) {
  const [bonusAvailable, setBonusAvailble] = useStickyState<boolean>(
    true,
    'bonus-available'
  )

  const history = useHistory()

  return (
    <main
      style={{
        fontFamily: 'arial',
        backgroundColor: theme.dark,
        color: theme.white,
        margin: '0 auto',
        height: '100vh',
        width: '100vw',
        maxWidth: '800px',
        overflowY: 'scroll',
        scrollSnapType: 'y proximity',
        scrollPaddingTop: 40,
        position: 'fixed',
        top: 0,
        left: 0,
      }}
    >
      <div
        style={{
          height: 60,
          display: 'flex',
          backgroundColor: theme.white,
          margin: 0,
          color: 'black',
          overflow: 'hidden',
          position: 'sticky',
          top: 0,
          left: 0,
          borderBottom: 'solid 2px grey',
          zIndex: 1,
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <div style={{ height: '100%', width: 60 }}>
          {gameType !== undefined && (
            <button
              style={{
                height: '100%',
                width: '100%',
                display: 'block',
                border: 'none',
                fontSize: 20,
                fontWeight: 500,
              }}
              onClick={() => history.push('/home')}
            >
              ←
            </button>
          )}
        </div>
        <Link
          style={{ textDecoration: 'none', color: theme.dark, flexGrow: 1 }}
          to="/"
        >
          <h1 css={styles.h1}>{title}</h1>
        </Link>
        <div
          style={{
            height: '100%',
            width: 60,
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: gameType !== undefined ? theme.grey : 'none',
          }}
        >
          {gameType === 'HUNTER' && (
            <LifeIcon
              style={{
                opacity: bonusAvailable ? 1 : 0.3,
              }}
              fillPrimary={theme.dark}
              fillSecondary={theme.dark}
              height={40}
              width={40}
              onClick={() => setBonusAvailble(!bonusAvailable)}
            />
          )}
        </div>
      </div>
      {children}
    </main>
  )
}

const styles = {
  h1: css`
    margin: 0;
    padding: 0;
    display: block;
    letter-spacing: 4px;
    text-align: center;
    font-weight: bold;
    font-size: 20px;
  `,
}
